import { request } from 'utils';

const prefix = '/api/categories';

export const getBuildings = ({
  label,
  baseId,
  noOfFloor,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Building',
    method: 'GET',
    params: {
      label,
      baseId,
      noOfFloor,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getFloorByBuilding = ({
  buildingId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Building/GetBuildingFloorCollection',
    method: 'GET',
    params: { buildingId, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getBuildingBases = async () =>
  request({
    prefix,
    url: '/BuildingBase',
    params: {},
    method: 'GET',
  });

export const getBuildingById = async id =>
  request({
    prefix,
    url: `/Building/GetById`,
    params: { id },
    method: 'GET',
  });

export const createBuildings = async values =>
  request({
    prefix,
    url: '/Building',
    params: {},
    data: { ...values },
    method: 'POST',
  });

export const updateBuildings = async values =>
  request({
    prefix,
    url: '/Building',
    params: {},
    data: { ...values },
    method: 'PUT',
  });

export const deleteBuilding = async id =>
  request({
    prefix,
    url: '/Building',
    params: {},
    data: { id },
    method: 'DELETE',
  });



