import React, { Component, Fragment } from 'react';
import { Card, Row, Button, Col, Icon, Form, Select, Radio, Input, message } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { TableData, TableEditCell, SelectAjax } from 'components/common';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import Page from 'components/LayoutComponents/Page';
import {
  searchClassCredit,
  getByClassCreditId,
  getClassCreditGroupByClassCreditScheduleId,
  createOrUpdateClassCreditSchedule,
  createOrUpdateClassCreditGroupSchedule,
  setIsLocked,
  getDayOfWeek,
  getStudySession,
  getBuildingRoom,
  getStaffBySubjectGroupId,
  getRegisterSession,
} from 'services/createSubject';
import { getSubjectGroups } from 'services/subjectGroup';
import { getBuildings } from 'services/building';
import moment from 'moment';
import './style.scss';

const RadioGroup = Radio.Group;
const { Option } = Select;

@Form.create({ name: 'CreateSubject' })
class CreateSubjectTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      saving: false,
      loadingTableCalendar: false,
      loadingTableCalendarGroup: false,
      paramSearch: {},
      currentClassSubject: {},
      currentCalendarClassCredit: {},
      calendarGroupClassCredit: {},
      reloadCreditTable: 0,
      calendarClassCredit: {},
      selectedRowCalendarClassCreditKeys: [],
      selectedRowCalendarGroupClassCreditKeys: [],
      dayOfWeeks: [],
      studySessions: [],
    };
    this.toggleLockState = this.toggleLockState.bind(this);
    this.handleChooseClassCredit = this.handleChooseClassCredit.bind(this);
    this.handleSaveCellDataCalendarClass = this.handleSaveCellDataCalendarClass.bind(this);
    this.handleSaveCellDataCalendarClassGroup = this.handleSaveCellDataCalendarClassGroup.bind(
      this
    );
  }

  componentDidMount() {
    this.initSelectData();
  }

  onSelectChangeCalendarClassCredit = (selectedRowKeys, selectedRows) => {
    if (selectedRows.length === 1) {
      this.handleChooseCalendarClassCredit(selectedRows[0].id);
      this.setState({ currentCalendarClassCredit: selectedRows[0] });
    } else {
      this.setState({
        calendarGroupClassCredit: {},
        currentCalendarClassCredit: {},
        selectedRowCalendarGroupClassCreditKeys: [],
      });
    }
    this.setState({ selectedRowCalendarClassCreditKeys: selectedRowKeys });
  };

  onSelectChangeCalendarGroupClassCredit = selectedRowKeys => {
    this.setState({ selectedRowCalendarGroupClassCreditKeys: selectedRowKeys });
  };

  getNextClassCodeCalendarSubject = classCode => {
    const { calendarClassCredit } = this.state;

    const calendarClassCreditArr = Object.values(calendarClassCredit);

    const filteredByClassCode = calendarClassCreditArr
      .map(item => item.syllableCode)
      .filter(item => item.startsWith(classCode));

    if (filteredByClassCode.length === 0) return 1;

    const maxCode = Math.max.apply(null, filteredByClassCode.map(item => item.split('_')[1]));

    return maxCode + 1;
  };

  getStaffBySubjectGroupId = async params => {
    const res = await getStaffBySubjectGroupId(params);
    res.data.items = res.data.staffs;
    return res;
  };

  createOrUpdateClassCreditGroupSchedule = async () => {
    this.setState({ saving: true });
    const { currentCalendarClassCredit, calendarGroupClassCredit } = this.state;
    const details = Object.values(calendarGroupClassCredit).map(item => {
      const {
        id = 0,
        ccsId = 0,
        buildingId,
        subjectGroupId,
        studySessionId,
        isNew,
        syllableCode = '',
        minStudentNo = 0,
        maxStudentNo = 0,
        periodInWeek = 0,
        dayOfWeek = 0,
        fromPeriodId = 0,
        toPeriodId = 0,
        roomId = 0,
        teacherId = '',
        fromWeek = 0,
        toWeek = 0,
        fromDate = '',
        toDate = '',
        isPractice = true,
      } = item;

      return {
        id: isNew ? 0 : id,
        ccsId,
        buildingId,
        subjectGroupId,
        studySessionId,
        syllableCode,
        minStudentNo: minStudentNo || 0,
        maxStudentNo: maxStudentNo || 0,
        periodInWeek: periodInWeek || 0,
        dayOfWeek,
        fromPeriodId,
        toPeriodId,
        roomId,
        teacherId,
        fromWeek: fromWeek || 0,
        toWeek: toWeek || 0,
        fromDate: fromDate || moment(),
        toDate: toDate || moment(),
        isPractice,
      };
    });
    const ccsId = currentCalendarClassCredit.id;
    const reqBody = {
      ccsId,
      details,
    };
    try {
      await createOrUpdateClassCreditGroupSchedule(reqBody);
      await this.handleChooseCalendarClassCredit(ccsId);
      message.success('Lu TKB thnh cng');
    } catch (err) {
      message.error('Lu TKB khng thnh cng');
    }

    this.setState({ saving: false });
  };

  createOrUpdateClassCreditSchedule = async () => {
    this.setState({ saving: true });
    const { currentClassSubject, calendarClassCredit } = this.state;
    const details = Object.values(calendarClassCredit).map(item => {
      const {
        id = 0,
        isNew,
        buildingId = 0,
        studySessionId = 0,
        subjectGroupId = 0,
        classCreditId = 0,
        syllableCode = '',
        dayOfWeek = 0,
        fromPeriodId = 0,
        toPeriodId = 0,
        roomId = 0,
        teacherId = '',
      } = item;
      const classCreditScheduleId = isNew ? 0 : id;
      return {
        buildingId,
        studySessionId,
        subjectGroupId,
        classCreditScheduleId,
        classCreditId,
        syllableCode,
        dayOfWeek,
        fromPeriodId,
        toPeriodId,
        roomId,
        teacherId,
      };
    });
    const classCreditId = currentClassSubject.classCreditId || 0;
    const reqBody = {
      classCreditId,
      details,
    };
    try {
      await createOrUpdateClassCreditSchedule(reqBody);
      await this.initSelectData();
      await this.handleChooseClassCredit(classCreditId);
      message.success('Lu TKB thnh cng');
    } catch (err) {
      message.error('Lu TKB khng thnh cng');
    }

    this.setState({ saving: false });
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.setState({ paramSearch: values });
      }
    });
  };

  handleResetSearchForm = () => {
    this.props.form.resetFields();
  };

  toggleLockState = async record => {
    await setIsLocked(record);
    this.refreshCreditTable();
  };

  refreshCreditTable = () => {
    const { reloadCreditTable } = this.state;
    this.setState({ reloadCreditTable: reloadCreditTable + 1 });
  };

  handleSaveCellDataCalendarClass = newData => {
    const { calendarClassCredit } = this.state;
    const newCalendarClassCredit = {
      ...calendarClassCredit,
      [newData.id]: newData,
    };
    this.setState({ calendarClassCredit: newCalendarClassCredit });
    return newData;
  };

  handleSaveCellDataCalendarClassGroup = newData => {
    const { calendarGroupClassCredit } = this.state;
    const newCalendarClassGroupCredit = {
      ...calendarGroupClassCredit,
      [newData.id]: newData,
    };
    this.setState({ calendarGroupClassCredit: newCalendarClassGroupCredit });
    return newData;
  };

  handleChooseClassCredit = async classCreditId => {
    this.setState({ loadingTableCalendar: true });
    this.setState({
      calendarClassCredit: {},
      currentCalendarClassCredit: {},
      calendarGroupClassCredit: {},
      selectedRowCalendarClassCreditKeys: [],
      selectedRowCalendarGroupClassCreditKeys: [],
    });
    const res = await getByClassCreditId({ classCreditId });

    const calendarClassCredit = res.data.items.reduce((obj, item) => {
      return {
        ...obj,
        [item.id]: item,
      };
    }, {});

    this.setState({ calendarClassCredit, loadingTableCalendar: false });
    return res;
  };

  handleChooseCalendarClassCredit = async classCreditScheduleId => {
    this.setState({ loadingTableCalendarGroup: true });
    const res = await getClassCreditGroupByClassCreditScheduleId({ classCreditScheduleId });

    const calendarGroupClassCredit = res.data.items.reduce((obj, item) => {
      return {
        ...obj,
        [item.id]: item,
      };
    }, {});

    this.setState({ calendarGroupClassCredit, loadingTableCalendarGroup: false });
    return res;
  };

  handleRemoveCalendarToClassCredit = () => {
    const {
      selectedRowCalendarClassCreditKeys,
      calendarClassCredit,
      currentCalendarClassCredit,
    } = this.state;

    selectedRowCalendarClassCreditKeys.forEach(removeId => {
      delete calendarClassCredit[removeId];
    });

    if (selectedRowCalendarClassCreditKeys.includes(currentCalendarClassCredit.id)) {
      this.setState({ currentCalendarClassCredit: {}, calendarGroupClassCredit: {} });
    }

    this.setState({ calendarClassCredit, selectedRowCalendarClassCreditKeys: [] });
  };

  handleAddCalendarToClassCredit = () => {
    const date = new Date();
    const now = date.getTime();
    const { currentClassSubject, calendarClassCredit } = this.state;
    const { classCode } = currentClassSubject;

    const nextClassCode = this.getNextClassCodeCalendarSubject(classCode);

    const newCalendar = {
      ...currentClassSubject,
      noOfStudent: undefined,
      syllableCode: `${classCode}_${nextClassCode}`,
      isNew: true,
      id: now,
    };
    const newCalendarClassCredit = {
      ...calendarClassCredit,
      [now]: newCalendar,
    };

    this.setState({ calendarClassCredit: newCalendarClassCredit });
  };

  handleAddCalendarGroup = () => {
    const { currentCalendarClassCredit, calendarGroupClassCredit } = this.state;

    if (currentCalendarClassCredit.isNew) return;

    const date = new Date();
    const now = date.getTime();

    const newCalendarGroupClassCredit = {
      ...currentCalendarClassCredit,
      noOfStudent: undefined,
      ccsId: currentCalendarClassCredit.id,
      id: now,
      isNew: true,
    };

    const newCalendarGroupClassCreditArr = {
      ...calendarGroupClassCredit,
      [now]: newCalendarGroupClassCredit,
    };

    this.setState({ calendarGroupClassCredit: newCalendarGroupClassCreditArr });
  };

  handleRemoveCalendarGroup = () => {
    const { selectedRowCalendarGroupClassCreditKeys, calendarGroupClassCredit } = this.state;

    selectedRowCalendarGroupClassCreditKeys.forEach(removeId => {
      delete calendarGroupClassCredit[removeId];
    });
    this.setState({ calendarGroupClassCredit, selectedRowCalendarGroupClassCreditKeys: [] });
  };

  initSelectData = async () => {
    const resStudySession = await getStudySession();
    const resDayOfWeeks = await getDayOfWeek();
    this.setState({
      studySessions: resStudySession.data.items,
      dayOfWeeks: resDayOfWeeks.data.items,
    });
  };

  classCreditColumns = [
    {
      title: 'Kha/M',
      dataIndex: 'isLocked',
      key: 'isLocked',
      align: 'center',
      render: (text, record) =>
        text ? (
          <Icon type="lock" onClick={() => this.toggleLockState(record)} />
        ) : (
          <Icon type="unlock" onClick={() => this.toggleLockState(record)} />
        ),
    },
    {
      title: 'K hiu tn lp TC',
      dataIndex: 'classCode',
      key: 'classCode',
    },
    {
      title: 'Tn mn',
      dataIndex: 'subjectLabel',
      key: 'subjectLabel',
    },
    {
      title: 'S TC',
      dataIndex: 'STC',
      key: 'STC',
    },
    {
      title: 'H s',
      dataIndex: 'multiplyBy',
      key: 'multiplyBy',
    },
    {
      title: 'S lp',
      dataIndex: 'noOfClass',
      key: 'noOfClass',
    },
    {
      title: 'S SV ti a',
      dataIndex: 'maxStudentNo',
      key: 'maxStudentNo',
    },
    {
      title: 'S SV ti thiu',
      dataIndex: 'minStudentNo',
      key: 'minStudentNo',
    },
    {
      title: 'S SV ng k',
      dataIndex: 'noOfStudent',
      key: 'noOfStudent',
    },
    {
      title: 'T.s tit',
      render: (text, record) =>
        record.theoryPeriod +
        record.practicePeriod +
        record.exercisePeriod +
        record.groupPeriod +
        record.selfPeriod,
    },
    {
      title: 'S.tit LT',
      dataIndex: 'theoryPeriod',
      key: 'theoryPeriod',
    },
    {
      title: 'S.tit TH',
      dataIndex: 'practicePeriod',
      key: 'practicePeriod',
    },
    {
      title: 'S.tit BT',
      dataIndex: 'exercisePeriod',
      key: 'exercisePeriod',
    },
    {
      title: 'S.tit TL',
      dataIndex: 'selfPeriod',
      key: 'selfPeriod',
    },
  ];

  classCreditCalendarColumns = [
    {
      title: 'Lp TC',
      dataIndex: 'syllableCode',
      key: 'syllableCode',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item style={{ width: '150px' }}>
          {form.getFieldDecorator(`${record.id}.syllableCode`, {
            initialValue: record.syllableCode,
          })(<Input onPressEnter={onSave} onBlur={onSave} refInput={setRefFormItem} />)}
        </Form.Item>
      ),
    },
    {
      title: 'Ti thiu',
      dataIndex: 'minStudentNo',
      key: 'minStudentNo',
    },
    {
      title: 'Ti a',
      dataIndex: 'maxStudentNo',
      key: 'maxStudentNo',
    },
    {
      title: 'S SV ng k',
      dataIndex: 'noOfStudent',
      key: 'noOfStudent',
    },
    {
      title: 'S tit tun',
      dataIndex: 'periodInWeek',
      key: 'periodInWeek',
    },
    {
      title: 'Th',
      dataIndex: 'dayOfWeek',
      key: 'dayOfWeek',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item>
          {form.getFieldDecorator(`${record.id}.dayOfWeek`, {
            initialValue: record.dayOfWeek ? record.dayOfWeek.toString() : undefined,
          })(
            <Select
              allowClear={false}
              onPressEnter={onSave}
              onBlur={onSave}
              refInput={setRefFormItem}
              className="select-day-custom-width"
            >
              {this.state.dayOfWeeks.map(dayOfWeek => (
                <Option key={dayOfWeek.id}>{dayOfWeek.label}</Option>
              ))}
            </Select>
          )}
        </Form.Item>
      ),
    },
    {
      title: 'T tit',
      dataIndex: 'fromPeriodId',
      key: 'fromPeriodId',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item style={{ width: '50px' }}>
          {form.getFieldDecorator(`${record.id}.fromPeriodId`, {
            initialValue: record.fromPeriodId,
          })(<Input onPressEnter={onSave} onBlur={onSave} refInput={setRefFormItem} />)}
        </Form.Item>
      ),
    },
    {
      title: 'n tit',
      dataIndex: 'toPeriodId',
      key: 'toPeriodId',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item style={{ width: '50px' }}>
          {form.getFieldDecorator(`${record.id}.toPeriodId`, {
            initialValue: record.toPeriodId,
          })(<Input onPressEnter={onSave} onBlur={onSave} refInput={setRefFormItem} />)}
        </Form.Item>
      ),
    },
    {
      title: 'Ca hc',
      dataIndex: 'studySessionId',
      key: 'studySessionId',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item>
          {form.getFieldDecorator(`${record.id}.studySessionId`, {
            initialValue: record.studySessionId ? record.studySessionId.toString() : undefined,
          })(
            <Select
              allowClear={false}
              onPressEnter={onSave}
              onBlur={onSave}
              refInput={setRefFormItem}
              className="select-day-custom-width"
            >
              {this.state.studySessions.map(studySession => (
                <Option key={studySession.id}>{studySession.label}</Option>
              ))}
            </Select>
          )}
        </Form.Item>
      ),
    },
    {
      title: 'Ta nh',
      dataIndex: 'buildingId',
      key: 'buildingId',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item>
          {form.getFieldDecorator(`${record.id}.buildingId`, {
            initialValue: record.buildingId,
          })(
            <SelectAjax
              allowClear={false}
              service={getBuildings}
              onPressEnter={onSave}
              onBlur={onSave}
              callbackChangeValue={onSave}
              refInput={setRefFormItem}
              className="select-day-custom-width"
            />
          )}
        </Form.Item>
      ),
    },
    {
      title: 'Phng hc',
      dataIndex: 'roomId',
      key: 'roomId',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item>
          {form.getFieldDecorator(`${record.id}.roomId`, {
            initialValue: record.roomId,
          })(
            <SelectAjax
              allowClear={false}
              setFieldsValue={form.setFieldsValue}
              params={{ buildingId: record.buildingId }}
              allowGetData={!!record.buildingId}
              disabled={!record.buildingId}
              keyValue="value"
              service={getBuildingRoom}
              onPressEnter={onSave}
              onBlur={onSave}
              refInput={setRefFormItem}
              className="select-day-custom-width"
            />
          )}
        </Form.Item>
      ),
    },
    {
      title: 'B mn',
      dataIndex: 'subjectGroupId',
      key: 'subjectGroupId',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item>
          {form.getFieldDecorator(`${record.id}.subjectGroupId`, {
            initialValue: record.subjectGroupId,
          })(
            <SelectAjax
              allowClear={false}
              service={getSubjectGroups}
              onPressEnter={onSave}
              onBlur={onSave}
              callbackChangeValue={onSave}
              refInput={setRefFormItem}
              className="select-day-custom-width"
            />
          )}
        </Form.Item>
      ),
    },
    {
      title: 'Gio vin',
      dataIndex: 'teacherId',
      key: 'teacherId',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item style={{ width: '250px' }}>
          {form.getFieldDecorator(`${record.id}.teacherId`, {
            initialValue: record.teacherId,
          })(
            <SelectAjax
              allowClear={false}
              setFieldsValue={form.setFieldsValue}
              allowGetData={!!record.subjectGroupId}
              disabled={!record.subjectGroupId}
              params={{ subjectGroupId: record.subjectGroupId }}
              keyValue="staffId"
              keyLabel="fullname"
              service={this.getStaffBySubjectGroupId}
              onPressEnter={onSave}
              onBlur={onSave}
              refInput={setRefFormItem}
              className="select-day-custom-width"
            />
          )}
        </Form.Item>
      ),
    },
    {
      title: 'T tun',
      dataIndex: 'fromWeek',
      key: 'fromWeek',
    },
    {
      title: 'n tun',
      dataIndex: 'toWeek',
      key: 'toWeek',
    },
    {
      title: 'T ngy',
      dataIndex: 'fromDate',
      key: 'fromDate',
      render: text => <div style={{ width: '100px' }}>{moment(text).format('DD-MM-YYYY')}</div>,
    },
    {
      title: 'n ngy',
      dataIndex: 'toDate',
      key: 'toDate',
      render: text => <div style={{ width: '100px' }}>{moment(text).format('DD-MM-YYYY')}</div>,
    },
  ];

  classCreditCalendarGroupColumns = [
    {
      title: 'Lp TC',
      dataIndex: 'syllableCode',
      key: 'syllableCode',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item style={{ width: '150px' }}>
          {form.getFieldDecorator(`${record.id}.syllableCode`, {
            initialValue: record.syllableCode,
          })(<Input onPressEnter={onSave} onBlur={onSave} refInput={setRefFormItem} />)}
        </Form.Item>
      ),
    },
    {
      title: 'Ti thiu',
      dataIndex: 'minStudentNo',
      key: 'minStudentNo',
    },
    {
      title: 'Ti a',
      dataIndex: 'maxStudentNo',
      key: 'maxStudentNo',
    },
    {
      title: 'S SV ng k',
      dataIndex: 'noOfStudent',
      key: 'noOfStudent',
    },
    {
      title: 'S tit tun',
      dataIndex: 'periodInWeek',
      key: 'periodInWeek',
    },
    {
      title: 'Th',
      dataIndex: 'dayOfWeek',
      key: 'dayOfWeek',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item>
          {form.getFieldDecorator(`${record.id}.dayOfWeek`, {
            initialValue: record.dayOfWeek ? record.dayOfWeek.toString() : undefined,
          })(
            <Select
              allowClear={false}
              onPressEnter={onSave}
              onBlur={onSave}
              refInput={setRefFormItem}
              className="select-day-custom-width"
            >
              {this.state.dayOfWeeks.map(dayOfWeek => (
                <Option key={dayOfWeek.id}>{dayOfWeek.label}</Option>
              ))}
            </Select>
          )}
        </Form.Item>
      ),
    },
    {
      title: 'T tit',
      dataIndex: 'fromPeriodId',
      key: 'fromPeriodId',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item style={{ width: '50px' }}>
          {form.getFieldDecorator(`${record.id}.fromPeriodId`, {
            initialValue: record.fromPeriodId,
          })(<Input onPressEnter={onSave} onBlur={onSave} refInput={setRefFormItem} />)}
        </Form.Item>
      ),
    },
    {
      title: 'n tit',
      dataIndex: 'toPeriodId',
      key: 'toPeriodId',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item style={{ width: '50px' }}>
          {form.getFieldDecorator(`${record.id}.toPeriodId`, {
            initialValue: record.toPeriodId,
          })(<Input onPressEnter={onSave} onBlur={onSave} refInput={setRefFormItem} />)}
        </Form.Item>
      ),
    },
    {
      title: 'Ca hc',
      dataIndex: 'studySessionId',
      key: 'studySessionId',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item>
          {form.getFieldDecorator(`${record.id}.studySessionId`, {
            initialValue: record.studySessionId ? record.studySessionId.toString() : undefined,
          })(
            <Select
              allowClear={false}
              onPressEnter={onSave}
              onBlur={onSave}
              refInput={setRefFormItem}
              className="select-day-custom-width"
            >
              {this.state.studySessions.map(studySession => (
                <Option key={studySession.id}>{studySession.label}</Option>
              ))}
            </Select>
          )}
        </Form.Item>
      ),
    },
    {
      title: 'Ta nh',
      dataIndex: 'buildingId',
      key: 'buildingId',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item>
          {form.getFieldDecorator(`${record.id}.buildingId`, {
            initialValue: record.buildingId,
          })(
            <SelectAjax
              allowClear={false}
              service={getBuildings}
              onPressEnter={onSave}
              onBlur={onSave}
              refInput={setRefFormItem}
              className="select-day-custom-width"
            />
          )}
        </Form.Item>
      ),
    },
    {
      title: 'Phng hc',
      dataIndex: 'roomId',
      key: 'roomId',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item>
          {form.getFieldDecorator(`${record.id}.roomId`, {
            initialValue: record.roomId,
          })(
            <SelectAjax
              allowClear={false}
              setFieldsValue={form.setFieldsValue}
              params={{ buildingId: record.buildingId }}
              allowGetData={!!record.buildingId}
              disabled={!record.buildingId}
              keyValue="value"
              service={getBuildingRoom}
              onPressEnter={onSave}
              onBlur={onSave}
              refInput={setRefFormItem}
              className="select-day-custom-width"
            />
          )}
        </Form.Item>
      ),
    },
    {
      title: 'B mn',
      dataIndex: 'subjectGroupId',
      key: 'subjectGroupId',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item>
          {form.getFieldDecorator(`${record.id}.subjectGroupId`, {
            initialValue: record.subjectGroupId,
          })(
            <SelectAjax
              allowClear={false}
              service={getSubjectGroups}
              onPressEnter={onSave}
              onBlur={onSave}
              refInput={setRefFormItem}
              className="select-day-custom-width"
            />
          )}
        </Form.Item>
      ),
    },
    {
      title: 'Gio vin',
      dataIndex: 'teacherId',
      key: 'teacherId',
      alwayShowEdit: true,
      editable: true,
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item style={{ width: '250px' }}>
          {form.getFieldDecorator(`${record.id}.teacherId`, {
            initialValue: record.teacherId,
          })(
            <SelectAjax
              allowClear={false}
              setFieldsValue={form.setFieldsValue}
              allowGetData={!!record.subjectGroupId}
              disabled={!record.subjectGroupId}
              params={{ subjectGroupId: record.subjectGroupId }}
              keyValue="staffId"
              keyLabel="fullname"
              service={this.getStaffBySubjectGroupId}
              onPressEnter={onSave}
              onBlur={onSave}
              refInput={setRefFormItem}
              className="select-day-custom-width"
            />
          )}
        </Form.Item>
      ),
    },
    {
      title: 'T tun',
      dataIndex: 'fromWeek',
      key: 'fromWeek',
    },
    {
      title: 'n tun',
      dataIndex: 'toWeek',
      key: 'toWeek',
    },
    {
      title: 'T ngy',
      dataIndex: 'fromDate',
      key: 'fromDate',
      render: text => <div style={{ width: '100px' }}>{moment(text).format('DD-MM-YYYY')}</div>,
    },
    {
      title: 'n ngy',
      dataIndex: 'toDate',
      key: 'toDate',
      render: text => <div style={{ width: '100px' }}>{moment(text).format('DD-MM-YYYY')}</div>,
    },
  ];

  render() {
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const {
      paramSearch,
      currentClassSubject,
      calendarClassCredit,
      reloadCreditTable,
      selectedRowCalendarClassCreditKeys,
      selectedRowCalendarGroupClassCreditKeys,
      currentCalendarClassCredit,
      calendarGroupClassCredit,
      loadingTableCalendar,
      loadingTableCalendarGroup,
      saving,
    } = this.state;

    const dataSourceCaladarClassCreditSource = Object.values(calendarClassCredit);
    const dataSourceCaladarGroupClassCreditSource = Object.values(calendarGroupClassCredit);

    const ignoreParams = {
      courseId: true,
    };

    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn H!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Kha',
          },
        ],
      },
    };

    const rowCalendarClassCreditSelection = {
      selectedRowKeys: selectedRowCalendarClassCreditKeys,
      onChange: this.onSelectChangeCalendarClassCredit,
    };

    const rowCalendarGroupClassCreditSelection = {
      selectedRowKeys: selectedRowCalendarGroupClassCreditKeys,
      onChange: this.onSelectChangeCalendarGroupClassCredit,
    };

    const trainTypeId = getFieldValue('trainTypeId');

    return (
      <Card title={this.title}>
        <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
          <Row gutter={24}>
            <FilterPathEducate
              form={this.props.form}
              classes
              column={6}
              optionFieldDecorator={optionFieldDecorator}
              ignoreParams={ignoreParams}
            />
          </Row>
          <Row gutter={24}>
            <Col span={6}>
              <Form.Item label="t ng k">
                {getFieldDecorator('registerSessionId', {})(
                  <SelectAjax
                    allowClear={false}
                    setFieldsValue={setFieldsValue}
                    params={{
                      trainTypeId,
                    }}
                    allowGetData={!!trainTypeId}
                    service={getRegisterSession}
                  />
                )}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label="K hiu mn">{getFieldDecorator('subjectId')(<Input />)}</Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item>
                {getFieldDecorator('isAllSubject', { initialValue: true })(
                  <RadioGroup style={{ marginTop: '8px' }}>
                    <Radio value>Tt c cc mn</Radio>
                    <Radio value={false}>Mn chung</Radio>
                  </RadioGroup>
                )}
              </Form.Item>
            </Col>
            <Col span={6}>
              <div className="btn-group">
                <BtnSearch htmlType="submit" />
                <BtnClear onClick={this.handleResetSearchForm} />
              </div>
            </Col>
          </Row>
        </Form>
        <Row>
          <Col span={24}>
            <TableData
              id="class-credit-table"
              reload={reloadCreditTable}
              bordered
              service={searchClassCredit}
              columns={this.classCreditColumns}
              paramSearch={paramSearch}
              allowGetData={!!paramSearch.trainTypeId && !!paramSearch.courseId}
              rowKey="classCreditId"
              onRow={record => {
                return {
                  onClick: event => {
                    event.preventDefault();
                    this.setState({ currentClassSubject: record });
                    this.handleChooseClassCredit(record.classCreditId);
                  },
                  className:
                    currentClassSubject.classCreditId === record.classCreditId
                      ? 'active-class-credit'
                      : {},
                };
              }}
            />
          </Col>
        </Row>
        <Fragment>
          <Row style={{ marginBottom: 10 }}>
            <Col span={24}>
              <Button
                style={{ marginRight: 5 }}
                icon="save"
                onClick={this.createOrUpdateClassCreditSchedule}
                loading={saving}
              >
                Lu TKB
              </Button>
              <Button
                style={{ marginRight: 5 }}
                icon="plus"
                onClick={this.handleAddCalendarToClassCredit}
                disabled={!Object.keys(currentClassSubject).length}
              >
                Thm lp
              </Button>
              <Button
                style={{ marginRight: 5 }}
                icon="close"
                disabled={selectedRowCalendarClassCreditKeys.length === 0}
                onClick={this.handleRemoveCalendarToClassCredit}
              >
                Xa lp
              </Button>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TableEditCell
                loading={loadingTableCalendar || saving}
                id="calendar-class-credit-table"
                rowSelection={rowCalendarClassCreditSelection}
                bordered
                dataSource={dataSourceCaladarClassCreditSource}
                columns={this.classCreditCalendarColumns}
                handleSaveCellData={this.handleSaveCellDataCalendarClass}
                rowKey="id"
                notPagination
                onRow={record => {
                  return {
                    className:
                      currentCalendarClassCredit.id === record.id ? 'active-class-credit' : {},
                  };
                }}
              />
            </Col>
          </Row>
        </Fragment>
        <Fragment>
          <Row style={{ marginBottom: 10 }}>
            <Col span={24}>
              <Button
                style={{ marginRight: 5 }}
                icon="save"
                loading={saving}
                onClick={this.createOrUpdateClassCreditGroupSchedule}
              >
                Lu TKB
              </Button>
              <Button
                style={{ marginRight: 5 }}
                icon="plus"
                onClick={this.handleAddCalendarGroup}
                disabled={!Object.keys(currentCalendarClassCredit).length}
              >
                Thm lp
              </Button>
              <Button
                style={{ marginRight: 5 }}
                icon="close"
                disabled={selectedRowCalendarGroupClassCreditKeys.length === 0}
                onClick={this.handleRemoveCalendarGroup}
              >
                Xa lp
              </Button>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TableEditCell
                loading={loadingTableCalendarGroup || saving}
                id="calendar-class-credit-table"
                rowSelection={rowCalendarGroupClassCreditSelection}
                bordered
                dataSource={dataSourceCaladarGroupClassCreditSource}
                columns={this.classCreditCalendarGroupColumns}
                handleSaveCellData={this.handleSaveCellDataCalendarClassGroup}
                rowKey="id"
                notPagination
              />
            </Col>
          </Row>
        </Fragment>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <CreateSubjectTable />
  </Page>
);



