import { request } from 'utils';

const prefix = '/api/education';
const categoriesPrefix = '/api/categories';
const organizationPrefix = '/api/organization';

export const searchClassCredit = ({
  registerSessionId,
  subjectId,
  isAllSubject = false,
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/ClassCredit/SearchClassCredit',
    method: 'GET',
    params: {
      registerSessionId,
      subjectId,
      isAllSubject,
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
  });

export const getByClassCreditId = ({
  classCreditId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = false,
}) =>
  request({
    prefix,
    url: '/ClassCreditSchedule/GetByClassCreditId',
    method: 'GET',
    params: { classCreditId, page, pageSize, orderBy, isPagingEnabled },
  });

export const setIsLocked = ({ classCreditId, isLocked }) =>
  request({
    prefix,
    url: `/ClassCredit/SetIsLocked/${classCreditId}`,
    method: 'PUT',
    data: {
      id: classCreditId,
      isLocked: !isLocked,
    },
  });

export const getClassCreditGroupByClassCreditScheduleId = ({
  classCreditScheduleId,
  isPagingEnabled = false,
}) =>
  request({
    prefix,
    url: '/ClassCreditGroupSchedule/GetClassCreditGroupByClassCreditScheduleId',
    method: 'GET',
    params: { classCreditScheduleId, isPagingEnabled },
  });

export const createOrUpdateClassCreditSchedule = reqBody =>
  request({
    prefix,
    url: '/ClassCreditSchedule/CreateOrUpdateClassCreditSchedule',
    method: 'POST',
    data: reqBody,
  });

export const createOrUpdateClassCreditGroupSchedule = reqBody =>
  request({
    prefix,
    url: '/ClassCreditGroupSchedule/CreateOrUpdateClassCreditGroupSchedule',
    method: 'POST',
    data: reqBody,
  });

export const getDayOfWeek = () =>
  request({
    prefix,
    url: '/ClassCreditSchedule/DayOfWeek',
    method: 'GET',
  });

export const getStudySession = () =>
  request({
    prefix: categoriesPrefix,
    url: '/StudySession',
    method: 'GET',
  });

export const getBuildingRoom = ({ buildingId, isPagingEnabled = false }) =>
  request({
    prefix: categoriesPrefix,
    url: '/BuildingRoom/DataForSelect',
    method: 'GET',
    params: { buildingId, isPagingEnabled },
  });

export const getStaffBySubjectGroupId = ({ subjectGroupId, isPagingEnabled = false }) =>
  request({
    prefix: organizationPrefix,
    url: '/Staff/SubjectGroup/GetById',
    method: 'GET',
    params: { subjectGroupId, isPagingEnabled },
  });

export const getRegisterSession = ({
  trainTypeId,
  yearEduId,
  semesterId,
  isPagingEnabled = false,
}) =>
  request({
    prefix,
    url: '/RegisterSession/DataForSelect',
    method: 'GET',
    params: { trainTypeId, yearEduId, semesterId, isPagingEnabled },
  });



